@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@0;1&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: Lato;
}


.navbar {
  background-color: #00b87c;
  padding: 20px 60px;
  width: 100%;
  display: flex;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
select {
  display: inline;
  font-size: 24px;
  border: none;
  outline: none;
  background-color: #00b87c;
  color: white;
  font-family: 'Lato', sans-serif;
  padding: 0 8px;
  font-weight: 600;
}
.fal {
  font-size: 22px;
  color: white;
}
.open-btn {
  border: none;
  padding: 10px 14px;
  background: #fff;
  color: #00b87c;
  font-weight: 700;
  font-size: 13px;
  cursor: pointer;
}
.event-form {
  position: fixed;
  right: 0px;
  top: 0px;
  height: 100%;
  width: 425px;
  background-color: #fff;
  z-index: 5;
  border-radius: 3px;
}
@media only screen and (max-width: 425px) {
  .event-form {
    width: 100vw;
  }
}
.event-form h2 {
  color: #f6f6f6;
  font-family: Lato;
  font-weight: 900;
  margin-left: 30px;
  font-size: 25px;
}
.event-form header {
  background-color: #00b87c;
  width: 100%;
  display: flex;
  padding: 20px;
  justify-content: space-between;
}
.overlay {
  position: fixed;
  width: 100vw;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: 2;
}

.far {
  cursor: pointer;
  font-size: 25px;
  color: #fff;
  margin-right: 20px;
}


/* event form styles */


.btn__group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 11rem;
  margin: 2rem 0 1rem 3rem;
}

.btn {
  color: #00b87c;
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}

.btn.active {
  background: #acffe6;
  height: 36px;
  border-radius: 5px;
  box-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.1);
}
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.rowGrp {
  display: flex;
  align-items: center;
  margin: 1rem 0;
  width: 100%;
}
.textGrp {
  text-align: right;
  width: 100px;
  padding: 0.3rem;
}
.radioBtnText {
  margin: auto;
  font-size: 0.9rem;
  font-weight: 700;
  color: #3a3a3a;
  margin-right: 1rem;
  text-align: center;
}
.showMeGrp {
  width: 100%;
  padding: 1rem;
  font-family: Lato, sans-serif;
  font-size:700;
}
.radioGrp {
  display: flex;
  justify-content: flex-start;
  margin-left: 3rem;
}
.rowRadios {
  width: calc(100% - 100px);
  display: flex;
}
.radioBtnLabel {
  position: relative;
  color: #3a3a3a;
  font-size: 0.9rem;
  font-weight: 700;
  transition: all 400ms ease-in-out;
}
.radioBtnLabel:hover {
  cursor: pointer;
}
.radioBtnLabel::before {
  position: absolute;
  content: '';
  display: inline-flex;
  border: 1px solid #00b87c;
  width: 20px;
  height: 20px;
  top: 0;
  bottom: 0;
  left: -30px;
  border-radius: 50%;
  transition: all 400ms ease-in-out;
}
.radioBtnItem {
  display: none;
}
.radioBtnItem:checked + .radioBtnLabel::before {
  box-shadow: inset 0 0 0 0.2em #fff, inset 0 0 0 2em #00b87c;
}
.radioBtnItem:not(:checked) + .radioBtnLabel::before {
  border-color: #bebebe;
  color: #bebebe;
}
.radioBtnItem:not(:checked) + .radioBtnLabel {
  transition: all 400ms ease-in-out;
  color: #bebebe;
}
.radioBtnItem:not(:checked) + .radioBtnLabel:hover {
  color: #3a3a3a;
}
.radioBtnItem:not(:checked) + .radioBtnLabel:hover::before {
  border-color: #3a3a3a;
}

.card {
  margin: 30px 20px;
  border-left: 5px solid blue;
}

.timings,
.description {
  color: blue;
}

.timings {
  font-size: 12px;
}
.fa-sun {
  color: grey;
}

